import useContent from "../../../hooks/useContent";
import ContentCard from "../ContentCard"
import './Contact.scss';

interface AboutProps {
    active: boolean;
}

export default function Contact(props: AboutProps) {
    const { content } = useContent();
    return (
        <ContentCard active={props.active}>
        {
        content &&
        <div className="ContactContainer">
            <div className='Contact'>
                <h1> {content.contact.title} </h1>
                <div>
                    <ul>
                        <li> {content.contact.telephone.text}: {content.contact.telephone.number} </li>
                        <li> {content.contact.email.text}: {content.contact.email.address} </li>
                        <li> <a href={content.contact.github.url}>{content.contact.github.text}</a></li>
                        <li> <a href={content.contact.linkedin.url}>{content.contact.linkedin.text}</a> </li>
                    </ul>
                </div>
            </div>
        </div>
        }
        </ContentCard>
    )
}
