import { useEffect } from 'react';
import Navbar, { NavbarItem } from '../components/navbar/Navbar';
import { SupportedLanguages, Contents } from '../contexts/ContentContext';
import Content from '../components/content/Content';
import Footer from '../components/footer/Footer';
import About from '../components/content/about/About';
import Contact from '../components/content/contact/Contact';
import Experience from '../components/content/experience/Experience';
import Background from '../components/background/Background';
import useContent from '../hooks/useContent';
import './App.scss';

export default function App() {
  const { content, selectedContent, language, dispatch } = useContent();

  useEffect(() => {
    const clientLanguage = navigator.language.split('-')[0];
    if (clientLanguage === 'es') {
      dispatch({type: 'SET_LANGUAGE', payload: SupportedLanguages.SPANISH});
    } else {
      dispatch({type: 'SET_LANGUAGE', payload: SupportedLanguages.ENGLISH});
    }
  }, []);

  return (
    <div className='App'>
      <Navbar>
      {
        content && 
        <>
        <NavbarItem 
          title={content.about.title}
          selected={selectedContent === Contents.ABOUT}
          onClick={()=>dispatch({type: "SET_SELECTED_CONTENT", payload: Contents.ABOUT})} />
        <NavbarItem
          title={content.experience.title}
          selected={selectedContent === Contents.EXPERIENCE}
          onClick={()=>dispatch({type: "SET_SELECTED_CONTENT", payload: Contents.EXPERIENCE})} />
        {/* <NavbarItem 
          title={content.portfolio.title} 
          selected={selectedContent === Contents.PORTFOLIO} 
          onClick={()=>dispatch({type: "SET_SELECTED_CONTENT", payload: Contents.PORTFOLIO})} /> */}
        <NavbarItem 
          title={content.contact.title} 
          selected={selectedContent === Contents.CONTACT} 
          onClick={()=>dispatch({type: "SET_SELECTED_CONTENT", payload: Contents.CONTACT})} />
        </>
      }
      </Navbar>
      <Background />
      <Content>
        {
          <>
            <About active={selectedContent === Contents.ABOUT}/>
            {/* <Portfolio active={selectedContent === Contents.PORTFOLIO}/> */}
            <Experience active={selectedContent === Contents.EXPERIENCE}/>
            <Contact active={selectedContent === Contents.CONTACT}/>
          </>
        }
      </Content>
      
      <Footer>
          <div>
            3D Model by <a href='https://www.artstation.com/mnostva'>mnostva</a> | Icons from <a href='https://icons8.com'>Icons8</a>
          </div>
          <div className='LanguageSelect'>
            <div 
              className={language === SupportedLanguages.ENGLISH ? 'LanguageSelectItem selected' : 'LanguageSelectItem'} 
              onClick={()=>dispatch({type: 'SET_LANGUAGE', payload: SupportedLanguages.ENGLISH})}>English</div>
            <div 
              className={language === SupportedLanguages.SPANISH ? 'LanguageSelectItem selected' : 'LanguageSelectItem'}
              onClick={()=>dispatch({type: 'SET_LANGUAGE', payload: SupportedLanguages.SPANISH})}>Spanish</div>
          </div>
      </Footer>
    </div>
  );
}
