function replaceVar(variableName: string, value: string, text: string): string {
    // variables are wrapped in curly braces with a dollar sign in front
    const regex = new RegExp(`\\$\\{${variableName}\\}`, 'g');
    return text.replace(regex, value);
}

function replaceVars(variables: { [key: string]: string }, text: string): string {
    let replacedText = text;
    for (const [key, value] of Object.entries(variables)) {
        replacedText = replaceVar(key, value, replacedText);
    }
    return replacedText;
}

export { replaceVar, replaceVars };