import React from 'react'
import './Navbar.scss';
import NavbarItem from './NavbarItem';

interface NavbarProps {
      children?: React.ReactNode
}

export default function Navbar(props: NavbarProps) {
  return (
    <div className='NavbarContainer'>
        <div className='Navbar'>
            {props.children}
        </div>
    </div>
  )
}

export { NavbarItem };