import { createContext, useReducer } from 'react';

export enum Contents {
    ABOUT, EXPERIENCE, /* PORTFOLIO, */ CONTACT
}

export enum SupportedLanguages {
    ENGLISH, SPANISH
}

export interface ImagesData {
  avatars: string[];
  stack: string[][];
}


interface ContentState {
    selectedContent: Contents;
    language: SupportedLanguages;
}

interface ContentAction {
    type: string;
    payload?: string | SupportedLanguages | any;
  }
  
interface ContentContextProps extends ContentState {
    dispatch: React.Dispatch<ContentAction>;
}

export const ContentContext = createContext<ContentContextProps>({
    selectedContent: Contents.ABOUT,
    language: SupportedLanguages.ENGLISH,
    dispatch: () => {}
});

function contentReducer(state: ContentState, action: ContentAction) {
    switch (action.type) {
      case 'SET_LANGUAGE':
        return {
          ...state,
          language: action.payload as SupportedLanguages
        };
      case 'SET_SELECTED_CONTENT':
        return {
          ...state,
          selectedContent: action.payload as Contents
        };
      default:
        return state;
    }
  }

export function ContentContextProvider({ children }: { children: React.ReactNode }) {
    const [state, dispatch] = useReducer(contentReducer, { selectedContent: Contents.ABOUT, language: SupportedLanguages.ENGLISH });
  
    return (
      <ContentContext.Provider value={{ ...state, dispatch }}>
        {children}
      </ContentContext.Provider>
    );
  }