import useContent from "../../../hooks/useContent";
import ContentCard from "../ContentCard"
import Degree, { DegreeProps } from "./Degree";
import Job, {JobProps} from "./Job";
import './Experience.scss';
import { SupportedLanguages } from "../../../contexts/ContentContext";

interface ExperienceProps {
    active: boolean;
}

export default function Experience(props: ExperienceProps) {
    const { content, language } = useContent();
    return (
        <ContentCard active={props.active}>
        {
        content &&
            <div className="ExperienceContainer">
                <div className="Education">
                    <div>
                        <h2> {content.experience.education.title} </h2>
                        <div>{
                            content.experience.education.degrees.map((degree: DegreeProps, index: number) => {
                                return (
                                    <Degree key={index} school={degree.school} date={degree.date} degree={degree.degree} />
                                )
                            })
                        }</div>
                    </div>
                </div>
                <div className="Experience">
                    <div>
                        <div className="PDFDownload">
                            <a href={
                                language === SupportedLanguages.ENGLISH ? "files/cv_en.pdf" :
                                language === SupportedLanguages.SPANISH ? "files/cv_es.pdf" :
                                "files/cv_en.pdf"}>
                                <img src="icons/pdf_icon.png" alt="PDF" />
                            </a>
                        </div>
                        <h2> {content.experience.experience.title} </h2>
                        <div>{
                            content.experience.experience.jobs.map((job: JobProps, index: number) => {
                                return (
                                    <Job key={index} company={job.company} company_logo={job.company_logo} date={job.date} position={job.position} description={job.description} skills={job.skills} />
                                )
                            })
                        }</div>
                    </div>
                </div>
            </div>
        }
        </ContentCard>
    )
}
