import useContent from "../../../hooks/useContent";
import ContentCard from "../ContentCard"
import { replaceVars } from "../../../utils";
import { useEffect, useState } from "react";
import './About.scss';

interface AboutProps {
    active: boolean;
}

export default function About(props: AboutProps) {
    const { content, images } = useContent();
    const [about, setAbout] = useState<any>(null);
    const [currentImg, setCurrentImg] = useState<number>(0);
    const [isTransitioning, setIsTransitioning] = useState(false);

    useEffect(() => {
        if (!images) return;
        const interval = setInterval(() => {
            setIsTransitioning(true);
            setCurrentImg((currentImg + 1) % images.avatars.length);
            setTimeout(() => {
                setIsTransitioning(false);
              }, 100);
        }, 5000);

        console.log(images.stack);
        return () => clearInterval(interval);
    }, [images, currentImg]);

    useEffect(() => {
        if (!content) return;

        setAbout({
            title: content.about.title,
            body: replaceVars(
                {'age': String(Math.floor((new Date().getTime() - new Date(1997, 4, 25).getTime()) / 31536000000)),
                 'pet-age': String(Math.floor((new Date().getTime() - new Date(2016, 3, 1).getTime()) / 31536000000)),
                 'new-line': '\r\n'
                },
                content.about.body)
        });
        
    }, [content]);

    return (
        <ContentCard active={props.active}>
        {
        content && about && images &&
            <div className="About">
                <h1> {about.title} </h1>
                <div className="Description">
                    <div className="Avatar">
                    {
                        images.avatars.map((avatar: string, index: number) => {
                            return <img
                                key={index}
                                className={`AvatarImg ${currentImg === index ? '' : 'disabled'}`}
                                src={avatar}
                                alt="Me" />
                        })
                    }
                    </div>
                    <p> {about.body} </p>
                </div>
                <div className="Stack">
                    {
                        images.stack.map((stack: string[], index: number) => {
                            return <div key={index} className="StackRow">
                                {
                                    stack.map((tech: string, index: number) => {
                                        return <img
                                            key={index}
                                            className={'StackIcon'}
                                            src={tech}
                                            alt="Tech" />
                                    })
                                }
                            </div>
                        })
                    }
                </div>

            </div>
        }
        </ContentCard>
  )
}
