import { useEffect, useState } from 'react'
import { Canvas, useThree, useFrame, useLoader } from '@react-three/fiber'
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader'
import { Mesh, Color, TextureLoader, Vector3, Object3D } from 'three'
import { Contents } from '../../contexts/ContentContext'
import useContent from '../../hooks/useContent'
import './Background.scss'

function CameraController() {
    const { selectedContent } = useContent();
    const [targetPosition, setTargetPosition] = useState<Vector3>(new Vector3());
    const [targetLookAt, setTargetLookAt] = useState<Vector3>(new Vector3());
    const [ target ] = useState<Object3D>(new Object3D());

    const { camera } = useThree();
    
    useFrame(({ camera }, delta) => {
		camera.position.lerp(targetPosition, delta)
        target.position.lerp(targetLookAt, delta)
        camera.lookAt(target.position);
	})

    const modifyCamera = (position: Vector3, lookAt: Vector3) => {
        setTargetPosition(position);
        setTargetLookAt(lookAt);
    }

    useEffect(() => {
        switch (selectedContent) {
            case Contents.ABOUT:
                modifyCamera(new Vector3(-1, 2, 2), new Vector3(-2, 1, 0));
                break;
            case Contents.EXPERIENCE:
                modifyCamera(new Vector3(2, 3, -1), new Vector3(-1, 0, -2));
                break;
            case Contents.CONTACT:
                modifyCamera(new Vector3(3, 2, 0), new Vector3(0, 1, -1));
                break;
            default:
                break;
        }
    }, [selectedContent]);

    const onMouseMove = (event: MouseEvent) => {
        const x = event.clientX / window.innerWidth;
        const y = event.clientY / window.innerHeight;
        const xPosition = x * 0.1 - 0.05;
        const yPosition = y * 0.1 - 0.05;

        if (Math.abs(xPosition) < 0.02 && Math.abs(yPosition) < 0.02) return;

        const cameraForward = new Vector3();
        camera.getWorldDirection(cameraForward);

        const cameraRight = new Vector3();
        cameraRight.crossVectors(cameraForward, new Vector3(0, 1, 0));

        const cameraUp = new Vector3();
        cameraUp.crossVectors(cameraRight, cameraForward);

        const newPosition = new Vector3();
        newPosition.addVectors(camera.position, cameraRight.multiplyScalar(xPosition));
        newPosition.addVectors(newPosition, cameraUp.multiplyScalar(-yPosition));

        camera.position.lerpVectors(camera.position, newPosition, 0.1);
    }

    useEffect(() => {
        // add event listener for mouse move
        window.addEventListener('mousemove', onMouseMove);
        return () => {
            // remove event listener for mouse move
            window.removeEventListener('mousemove', onMouseMove);
        }
    }, []);

    return <primitive object={target} />;
}

function Lighting() {

    const [candleState, setCandleState] = useState<{position: Vector3, intensity: number}>({position: new Vector3(-1.5, 2, -1.5), intensity: 0.005});
    useFrame(({clock}, delta) => {
        const deltaPos = 0.01 * Math.cos(clock.getElapsedTime() * 5);
        const randomIntensity = Math.random() * (0.01 - 0.008) + 0.0075;
        setCandleState({position: new Vector3(-1.25, 2 + deltaPos, -1.5 + deltaPos), intensity: randomIntensity});
	})

    return (
        <>
        <ambientLight intensity={0.5} />
        <pointLight
            position={candleState.position}
            intensity={candleState.intensity}
            castShadow={true}
            distance={2}
            decay={0.1}
            shadow-mapSize-width={256}
            shadow-mapSize-height={256}
            color={new Color(255, 150, 25)}
        />
        <directionalLight
            position={[3, 10, 5]}
            intensity={0.7}
            castShadow={true}
            shadow-mapSize-width={2048}
            shadow-mapSize-height={2048}
        />
        </>
    )
}

function Scene() {
    const scene = useLoader(OBJLoader, 'models/office.obj');
    const texture = useLoader(TextureLoader, 'models/office.png');
    useEffect(() => {
        scene.traverse((child: any) => {
            if (child instanceof Mesh) {
                child.castShadow = true;
                child.receiveShadow = true;
                child.material.map = texture;
                child.material.needsUpdate = true;
            }
        });
    }, [scene, texture]);

    return (
        <primitive object={scene} />
    )    
}

export default function Background() {

    return (
        <div className='Background'>
            <Canvas camera={{fov: 60}} shadows={"soft"}>
                <CameraController />
                <Lighting />
                <Scene />
            </Canvas>
        </div>
  )
}
