import { useContext, useEffect, useState } from "react";
import { ContentContext, SupportedLanguages, ImagesData } from "../contexts/ContentContext";


export default function useContent() {
    const { selectedContent, language, dispatch } = useContext(ContentContext);
    const [data, setData] = useState<any>(null);
    const [content, setContent] = useState<any>(null);
    const [images, setImages] = useState<ImagesData>({ avatars: [], stack: [] });

    if (!{ selectedContent, language, dispatch })
        throw new Error("useContent must be used within a ContentContextProvider");

    useEffect(() => {
        try{
            fetch('data/content.json')
            .then(response => response.json())
            .then(data => setData(data));
        }
        catch(err){
            console.error(err)
    };
  }, [])

    useEffect(() => {
        if (!data) return;
        switch (language) {
            case SupportedLanguages.ENGLISH:
                setContent(data.en);
                break;
            case SupportedLanguages.SPANISH:
                setContent(data.es);
                break;
            default:
                setContent(data.en);
                break;
        }
        setImages({
            avatars: data.avatars.map((avatar: string) => `img/${avatar}`),
            stack: data.stackIcons.map((stack: string[]) => stack.map((tech: string) => `icons/${tech}`))
        });
    }, [data, language]);

    return { content, images, selectedContent, language, dispatch };
}